import React from 'react'
import Cardcuriosidad from '../Molecules/cardcuriosidad'
import withloader from '../Hoc/withloader.js'
const Curiosidadescontainer = ({curiosidad}) => (
<div  className="ed-grid  grid-container s-mt-2 m-grid-2 s-grid-1 xl-grid-2 lg-grid-3 center">
        
       { curiosidad.map(c => (
          <Cardcuriosidad 
            id = {c.id}
            title = {c.title}
            img = {c.img}
            curiosity ={c.curiosity}
            />)
          )
       }
        
</div>
)
export default withloader("curiosidad")(Curiosidadescontainer)