import Axios from "axios";
import React, { Component } from "react";
import Curiosidadescontainer from "../Organisms/curiosidadescontainer";

class Curiosidades extends Component {
  constructor(props) {
    super(props);
    this.state = {
      curiosidad: [],
    };
  }

  componentDidMount() {
    Axios.get(
      "https://my-json-server.typicode.com/FrapoDeveloper/curiosity-db/Curiosidades"
    ).then((response) => {
      //resolvemos la promesa
      this.setState({
        curiosidad: response.data,
      });
    });
  }

  render() {
    const { curiosidad } = this.state; //destructuracion de objetos
    return <Curiosidadescontainer curiosidad={curiosidad} />;
  }
}
export default Curiosidades;
